import fetch from 'isomorphic-unfetch';

const Index = props => {
  return (
    <div>
      <p>Hellodd Next.js</p>
      {props}
    </div>
  );
};

Index.getInitialProps = async () => {
	const response = await fetch('https://lbb-test.cdn.prismic.io/api/v2/documents/search?ref=master&lang=da-dk');
	console.log(response)
	const data = await response.json();

	return {
		...data.results.data
	};
};

export default Index;